

.cardBody{
    margin: auto;
    width: 80%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.cardItem {
    background-color: gray;
    padding: 20px;
    width: 25%;
    text-align: center;
    border-radius: 5px;
    color: honeydew;
    margin-bottom: 20px;
}

.arrow{
    width: 30px;
}

.button{
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
}



